@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,400i|PT+Sans);
/* RGB */
:root {
  --color: rgba(26, 40, 31, 1);
  --color-link: rgba(26, 40, 31, 0.7);
  --color0: white;
  --color1: rgba(26, 40, 31, 0.6);
  --color2: rgba(99, 82, 85, 1);
  --color3: rgba(206, 123, 145, 1);
  --color4: rgba(192, 232, 249, 1);
  --color5: rgba(164, 191, 189, 1);
  --color-green: #66BB6A;
}

* {
  margin: 0;
  padding: 0;
}

a {
  color: rgba(26, 40, 31, 0.7);
  color: var(--color-link);
}

a:hover {
  color: #bbb;
}

h1 {
  font-size: 1.5rem;
  margin: 2rem 0;
}

h2 {
  font-size: 1.2rem;
  margin: 2rem 0 1rem;
}

body {
  color: rgba(26, 40, 31, 1);
  color: var(--color);
  display: flex;
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Segoe UI", sans-serif;
  flex-direction: column;
  min-height: 100vh;
}


.wrap {
  align-items: stretch;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

header {
  align-items: center;
  background: white;
  box-shadow: 0 0 2.25rem #9da5ab;
  display: flex;
  font-family: 'PT Sans', sans-serif;
  justify-content: space-between;
  padding: 20px;
  position: relative;
  z-index: 1;
}

header h1 {
  margin: 0;
}

header p {
  font-weight: bold;
}

.container {
  align-items: stretch;
  display: flex;
  flex: 1 1;
}

main {
  flex-grow: 3;
  order: 2;
  padding: 20px 0;
  overflow: auto;
  position: relative;
}

aside {
  box-shadow: 0 0 0.5rem #9da5ab;
  font-weight: 300;
  flex: 1 1;
  order: 1;
  overflow: auto;
  padding: 20px;
  position: relative;
  min-width: 300px;
}

.search {
  margin: 1rem 0;
}

.search__input {
  border: none;
  border-bottom: 2px solid #ddd;
  font-size: 1rem;
  font-weight: 300;
  padding: 0.5rem 0;
  -webkit-transition: border-color 0.1s ease-out;
  transition: border-color 0.1s ease-out;
  width: 80%;
}

.search__input:focus {
  outline: 0;
  display: block;
  border-bottom: 2px solid var(--color5);
}

.search__group {
  display: block;
  font-size: 0.9rem;
  margin-top: 5px;
}



nav {
  position: absolute;
  padding-bottom: 20px;
}

nav p {
  margin: 4px 0;
}

details {
  padding-bottom: 7px;
}

summary {
  color: var(--color5);
  cursor: pointer;
  font-size: 0.9rem;
  margin-bottom: 4px;
  outline: none;
}

details > p {
  padding-left: 1em;
  margin-top: 3px;
}

details summary::-webkit-details-marker {
  -webkit-transform: translate(0, 1px) scale(0.7);
          transform: translate(0, 1px) scale(0.7);
  opacity: 0.5;
}

.count {
  border-radius: 8px;
  border: 1px solid var(--color5);
  background: var(--color5);
  color: var(--color0);
  display: inline-block;
  font-size: 0.5rem;
  margin-left: 5px;
  opacity: 0.6;
  padding: 0px 3px;
  position: relative;
  top: -1px;
}

.statusLabel {
  border-radius: 6px;
  color: var(--color0);
  display: inline-block;
  font-size: 0.5rem;
  padding: 2px 4px;
  margin-right: 0.25rem;
  opacity: 0.8;
}

.statusLabel--2 {
  background: var(--color-green);
}

.statusLabel--3 {
  background: var(--color4);
}

.statusLabel--4 {
  background: var(--color3);
}

.statusLabel--5 {
  background: var(--color5);
}


.method {
  margin-right: 5px;
  font-size: 12px;
  font-weight: 300;
  font-style: normal;
  border: 1px solid #999;
  color: #999;
  border-radius: 2px;
  padding: 2px 3px;
  display: inline-block;
}
.method.get {
  color: #66BB6A;
  border-color: #66BB6A;
}
.method.delete {
  color: #999;
  border-color: #999;
}
.method.put { 
  color: #9E9D24;
  border-color: #9E9D24;
}
.method.post {
  color: #F9A825;
  border-color: #F9A825;
}
.method.patch {
  color: #9C27B0;
  border-color: #9C27B0;
}

.definitions {
  color: var(--color1);
  margin: 1.5em 0;
}

.definitions h1 {
  font-size: 1em;
  color: var(--color5);
  margin-top: 0;
  margin-bottom: 1em;
  padding-left: 0;
}

.definitions > p,
.definitions > dl {
  margin: 0.25em 1em;
}

.definitions dl dt {
  color: var(--color3);
  font-weight: bold;
  font-style: italic;
}

.definitions dl dt:after {
  content: ': ';
}

.definitions dl > * {
  display: inline-block;
  margin-right: 0.5em;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  padding: 0;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

.body {
  color: var(--color1);
  padding: 0;
}

.body > .buttonGroup {
  margin-bottom: 1em;
  padding: 0 30px;
}

.body > .code {
  background: #efefef;
  font-weight: 300;
  font-size: 0.9rem;
  max-height: 500px;
  margin: 1em 0;
  overflow: auto;
  padding: 2em 2em;
}

.body > .code ul {
  background: #efefef !important;
}

.buttonGroup button {
  margin-right: 1em;
}

.tabButton {
  box-sizing: border-box;
  color: var(--color5);
  opacity: 0.4;
  font-size: 1rem;
  font-weight: bold;
  padding: 0 1px 3px;
}

.tabButton:hover {
  opacity: 1;
}

.tabButton:disabled {
  color: var(--color5);
  cursor: auto;
  cursor: initial;
  opacity: 1;
  border-bottom: 2px solid var(--color5);
}

.onlyButton.tabButton:disabled {
  border: none;
}

.contents > .agree {
  padding-top: 2rem;
  margin-bottom: 2rem;
}

.contents > .agree:first-child {
  padding-top: 0;
}

.agree > h1,
.agree > h2,
.agree > section,
.agree .description,
.agree .definitions {
  padding: 0 30px;
}

.agree h2 {
  color: var(--color);
  border-left: 2px #aaa solid;
  padding-left: 28px;
}

.title {
  align-items: center;
  border-left: 2px var(--color) solid;
  display: flex;
  font-family: 'Open Sans', sans-serif;
  font-style: italic;
  margin: 0 0 0 0;
}

.title > * {
  margin-right: 0.6rem;
}

.description {
  color: var(--color1);
  margin: 0.25rem 0;
}


.contents section h2 {
  color: var(--color);
  border-left: 2px #aaa solid;
  padding-left: 28px;
}

.contents {
  position: absolute;
  width: 100%;
}



